					<router-link tag="div" to="" class="van-form-preview__btn van-form-preview__btn_default">辅助操作</router-link>
				</div>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				showPreLoading: true
			}
		},
		mounted(){
			this.showPreLoading = false;
		}
	}
</script>

<style scoped>

</style>


